import { defineComponent, ref } from 'vue';
import LoginAccount from './login-account.vue';
import LoginPhone from './login-phone.vue';
export default defineComponent({
  components: {
    LoginAccount,
    LoginPhone
  },

  setup() {
    // 是否记住密码
    const isRemember = ref(false); // 获取accountRef子组件实例
    // <typeof LoginAccount>表示LoginAccount组件的类型
    // InstanceType<typeof LoginAccount>表示LoginAccount组件的实例类型

    const accountRef = ref();
    const phoneRef = ref();
    const currentTab = ref('account'); // 管理登录事件

    const handleLoginClick = () => {
      if (currentTab.value === 'account') {
        accountRef.value?.loginAction(isRemember.value);
      } else {
        phoneRef.value?.loginAction();
      }
    };

    return {
      isRemember,
      accountRef,
      phoneRef,
      currentTab,
      handleLoginClick
    };
  }

});